import {Component, OnInit} from '@angular/core';

@Component({
  selector: 'app-about-opinions',
  templateUrl: './about-opinions.component.html',
  styleUrls: ['./about-opinions.component.scss']
})
export class AboutOpinionsComponent implements OnInit {

  textArea;
  starDialogConfirmVisible: boolean;
  overallStars = 0;
  starsRatingsSent: boolean;

  understandingStars: number;
  useRateStars: number;
  recommendStars: number;
  email: string;

  constructor() {
  }

  ngOnInit(): void {
  }

  onSendTextArea(): void {
    this.textArea = '';
  }

  onStarSelected(): void {
    if (!this.starsRatingsSent) {
      this.starDialogConfirmVisible = true;
    }
  }

  onSendStarDialog(): void {
    this.starDialogConfirmVisible = false;
    // TODO send star info
    this.starsRatingsSent = true;
  }

  onDismissStarDialog(): void {
    this.starDialogConfirmVisible = false;
    this.overallStars = 0;
    this.understandingStars = 0;
    this.useRateStars = 0;
    this.recommendStars = 0;
    this.email = '';
  }
}
